// var extend = require('extend');
// var I18n = require("i18n-js");

import I18n from "i18n-js";
import i18nline from "i18nline/lib/extensions/i18n_js";
import preactI18nline from "preact-i18nline/dist/extensions/i18n_js";

i18nline(I18n);
preactI18nline(I18n);

// console.log(I18n)

// require("i18nline/lib/extensions/i18n_js")(I18n);
// require("preact-i18nline/dist/extensions/i18n_js")(I18n);

var translationFiles = require.context("./src/i18n", true, /\.json$/);
// console.log(translationFiles.keys());
translationFiles.keys().forEach(function(key) {
  extend(true, I18n, {translations: translationFiles(key)});
});

window.I18n = I18n;


